import React from "react";
import styled from "styled-components";
import useAxios from "../../../../api/Axios";
import { toast } from "react-toastify";
import { useMarksEntry } from "../../../../Context/MarksEntry";

const AddQuizz = () => {
  const marks = useMarksEntry();
  const axios = useAxios();
  const [students, setStudents] = React.useState([]);
  const [loading, setLoading] = React.useState(true);
  React.useEffect(() => {
    const getData = async () => {
      const url = "/student/all?id=" + marks.classInfo.classId;
      await axios.get(url).then((res) => {
        const data = res.data;
        setStudents(data);
      });
    };

    getData();
    setLoading(false);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [axios, marks.classInfo, marks.classInfo.classId]);

  const handleReset = () => {
    marks.handleBackToOption(false);
    marks.handleOpenQuizz(false);
  };

  const handleSubmit = async () => {
    try {
      const url = "/marks/update?label=Quizz/Test-" + marks.label;
      await axios.put(url, { data: students }).then((res) => {
        // handleSuccess(res.data);
        handleSuccess(res.data);
        setTimeout(() => {
          marks.handleBackToOption(false);
          marks.handleOpenQuizz(false);
        }, 1000);
      });
    } catch (error) {
      console.log(error);
      handleError(error.message);
    }
  };

  const handleSuccess = (msg) => {
    toast.success(msg, {
      position: "top-right",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      theme: "light",
    });
  };

  const handleError = (msg) => {
    toast.error(msg, {
      position: "top-right",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      theme: "light",
    });
  };

  const handleChange = (e, idx) => {
    const val = e.target.value;
    let fields1 = document.getElementsByName("id")[idx];
    let fields2 = document.getElementsByName("name")[idx];
    let fields = document.getElementsByName("quizz")[idx];

    if (marks.classInfo.courseType === "Lab") {
      if (val <= 10 && val >= 0) {
        fields1.style.background = "transparent";
        fields2.style.background = "transparent";
        fields.style.background = "transparent";
        students[idx][`test${marks.label}`] = Number(val);
      } else {
        fields.value = students[idx][`test${marks.label}`];
        fields.style.background = "rgba(255, 121, 121,0.2)";
        fields1.style.background = "rgba(255, 121, 121,0.2)";
        fields2.style.background = "rgba(255, 121, 121,0.2)";
        handleError(`Input Value(${val}) large from 10`);
      }
    } else {
      if (val <= 15 && val >= 0) {
        fields.style.background = "transparent";
        fields1.style.background = "transparent";
        fields2.style.background = "transparent";
        students[idx][`test${marks.label}`] = Number(val);
      } else {
        fields.value = students[idx][`test${marks.label}`];
        fields.style.background = "rgba(255, 121, 121,0.2)";
        fields1.style.background = "rgba(255, 121, 121,0.2)";
        fields2.style.background = "rgba(255, 121, 121,0.2)";
        handleError(`Input Value(${val}) large from 15`);
      }
    }
  };

  return (
    <AttendancePanel open={marks.openQuizz}>
      <Form>
        <Title>Add Marks for Quizz/Test - {marks.label}</Title>
        {loading ? (
          "Loading..."
        ) : (
          <InputPanel>
            {students.map((dt, idx) => {
              return (
                <React.Fragment key={idx}>
                  <p
                    style={{
                      marginBottom: 0,
                      fontWeight: 500,
                      marginTop: "10px",
                    }}
                  >
                    Index No: #{idx + 1}
                  </p>
                  <FlexRow>
                    <Input
                      disabled
                      name="id"
                      className="stuId"
                      type="Number"
                      defaultValue={dt.stuId}
                    />
                    <Input
                      disabled
                      name="name"
                      type="text"
                      defaultValue={dt.stuName}
                    />
                    <InputSubPanel
                      style={{
                        display: "flex",
                        flexWrap: "nowrap",
                        justifyItems: "stretch",
                        alignItems: "stretch",
                      }}
                    >
                      <Input
                        style={{ width: "100%" }}
                        name="quizz"
                        type="Number"
                        max={marks.classInfo.courseType === "Lab" ? "10" : "5"}
                        min="0"
                        defaultValue={dt[`test${marks.label}`]}
                        onChange={(e) => handleChange(e, idx)}
                        onWheel={() => document.activeElement.blur()}
                      />
                      <Text>
                        {marks.classInfo.courseType === "Lab" ? 10 : 15}
                      </Text>
                    </InputSubPanel>
                  </FlexRow>
                </React.Fragment>
              );
            })}
            <ButtonPanel>
              <Button onClick={handleReset}>Back</Button>
              <Button onClick={handleSubmit}>Submit</Button>
            </ButtonPanel>
          </InputPanel>
        )}
      </Form>
    </AttendancePanel>
  );
};

export default AddQuizz;

const AttendancePanel = styled.div`
  display: ${(props) => (props.open === true ? "block" : "none")};
  width: 750px;
  border-radius: 5px;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (max-width: 760px) {
    width: 95%;
  }
`;

const Form = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
`;

const Input = styled.input`
  padding: 0.4rem 0.2rem;
  border-radius: 3px;
  margin-bottom: 0.3rem;
  border: 1px solid rgba(3, 3, 3, 0.2);

  &::placeholder {
    color: rgba(3, 3, 3, 0.2);
  }
`;

const Button = styled.button`
  font-weight: 500;
  color: #fff;
  border-radius: 5px;
  min-width: 100px;
  &:nth-child(1) {
    background: rgba(235, 77, 75, 1);
    margin-right: 5px;
  }

  &:nth-child(2) {
    background: rgba(72, 52, 212, 1);
  }

  &:hover {
    &:nth-child(1) {
      background: rgba(235, 100, 75, 1);
    }

    &:nth-child(2) {
      background: rgba(72, 100, 212, 1);
    }
  }

  @media (max-width: 560px) {
    width: 100%;
  }
`;

const ButtonPanel = styled.div`
  display: flex;
  justify-content: flex-end;
  margin: 1rem 0;
  padding: 0.2rem 0;
`;

const Title = styled.h1`
  font-size: 1.2rem;
  font-weight: 500;
`;

const InputPanel = styled.div`
  border: 1px solid rgba(3, 3, 3, 0.3);
  display: flex;
  flex-direction: column;
  margin-bottom: 0.2rem;
  padding: 0.2rem 3rem;
  position: relative;
  border-radius: 3px;
  width: 700px;

  @media (max-width: 760px) {
    width: 90%;
    padding: 0.2rem 1rem;
  }
`;

const FlexRow = styled.div`
  display: grid;
  grid-template-columns: auto auto auto;
  overflow: hidden;
  width: 100%;

  @media (max-width: 560px) {
    grid-template-columns: auto;
  }
`;

const Text = styled.span`
  margin: 0;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 0.3rem;
  padding-left: 5px;
  position: absolute;
  right: 2px;
  background: rgba(50, 200, 100, 1);
  width: 45px;
  top: 0.18rem;
  bottom: 0.46rem;
  color: #fff;
  font-weight: 600;
  border: 1px solid rgba(3, 3, 3, 0.4);
  border-radius: 3px;
`;

const InputSubPanel = styled.div`
  position: relative;
`;
